
th {
  border-bottom: 1px solid #2b2b2b;
}

td {
  padding: 8px;
  user-select: none;
  border-bottom: 1px solid #4b4b4b;
}

.selectedRow {
  background-color: #bebebe;
}

select {
  width: 300px;
  height: 28px;
  margin: 0 16px 16px 16px;
  font-size: 18px;
  border-radius: 3px;
  background-color: #f0f0f0;
}

button {
  width: 142px;
  height: 28px;
  margin: 0 8px;
}

#Panel-Header {
  display: flex;
  justify-content: left;
  width: 100%;
  height: 48px;
  font-size: 16px;
  background-color: #282c34;
}

#Panel-Header>p {
  margin: auto auto;
  text-transform: uppercase;
}

#Panel-Body {
  display: flex;
  width: 100%;
  min-height: calc(100vh - 48px);
  overflow-y: hidden;
}

#Panel-Menu {
  display: none;
  flex-direction: column;
  min-width: 250px;
  max-width: 250px;
  min-height: 100%;
  background-color: #282c34;
}

#Panel-Menu-Container {
  z-index: 2;
  width: fit-content;
  min-width: 10px;
  background-color: #282c34;
  box-shadow: 1px 0px 8px #282c34;
}

#Panel-Menu-Container:hover>#Panel-Menu, #Panel-Menu:hover {
  display: flex !important;
  flex-direction: column !important;
}

#Panel-Menu-Icons {
  display: flex;
  height: 48px;
  overflow-y: hidden;
  border-bottom: 1px solid #363b46;
}

.Panel-Menu-Icon {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 48px;
  cursor: pointer;
}

.Panel-Menu-Icon:hover {
  background-color: #363b46;
}

.Panel-Menu-Icon>img {
  width: 32px;
  height: 32px;
  margin: auto 0;
}

.Panel-Menu-Header {
  font-size: 24px;
  user-select: none;
  pointer-events: none;
  color: rgb(198, 201, 207);
  text-align: left;
  padding: 48px 0 16px 8px;
  border-bottom: 1px solid rgb(192, 192, 192);
}

.Panel-Menu-Option {
  cursor: pointer;
  user-select: none;
  color: rgb(198, 201, 207);
  text-align: left;
  padding: 16px 0 16px 8px;
  border-bottom: 1px solid rgb(192, 192, 192);
}

.Panel-Menu-Option:hover {
  background-color: #363b46;
}

.panel-users-user {
  display: flex;
  cursor: pointer;
  height: 48px;
  max-width: 500px;
  border-radius: 5px;
  padding: 8px 18px 8px 18px;
  margin: 15px auto;
  background-color: rgb(231, 227, 222);
}

.panel-users-user>p {
  color: black;
  width: 100%;
  height: 32px;
  padding: 8px 0 0 0;
  margin: auto 0 auto 0;
  user-select: none;
}

.panel-users-user>img {
  width: 32px;
  height: 32px;
  margin: auto 18px auto 0;
  user-select: none;
}

.text-banner {
  display: flex;
  justify-content: left;
  height: 48px;
  max-width: 90%;
  border-radius: 5px;
  padding: 8px 18px;
  margin: 16px 0;
  background-color: rgb(231, 227, 222);
}

.text-banner>b {
  user-select: none;
}

.text-banner>p {
  cursor: pointer;
  user-select: all;
}

.text-banner>p, .text-banner>b {
  color: #161920;
  margin: auto 8px;
}

.markdown-container {
  overflow: auto;
  width: calc(50% - 64px);
  background-color: rgb(231, 227, 222);
}

.markdown-container>h1,
.markdown-container>h2 {
  text-align: center;
  margin: unset;
  padding: 18px;
  color:rgb(32, 32, 32);
}

.markdown-container>h3,
.markdown-container>h4,
.markdown-container>h5,
.markdown-container>h6 {
  text-align: left;
  margin: unset;
  padding: 18px;
  color:rgb(32, 32, 32);
}

.markdown-container>p {
  margin: unset;
  padding: 18px;
  color:rgb(32, 32, 32);
}

.Panel-Option-Header {
  color: rgb(32, 32, 34);
  text-align: center;
  margin: 48px auto;
  text-decoration: underline;
  letter-spacing: 2px;
}

.Panel-Option-Header>img {
  width: 16px;
  height: 16px;
  margin-left: 8px;
}

#Panel-Content {
  width: 100%;
  height: calc(100vh - 48px);
  overflow-y: auto;
  padding: 0 5%;
}

#Panel-Content-Left, #Panel-Content-Right {
  width: 50%;
}


#Panel-Content-Container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#E-Panel-Login {
  display: flex;
  flex-direction: row;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100vw;
  height: calc(120vh - 48px);
}

#E-Panel-Login-Left {
  display: flex;
  user-select: none;
  pointer-events: none;

  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 50vw;
  max-width: 50vw;
  height: 100%;

  margin-top: -10%;
  margin-bottom: -5%;

  border-right: 2px solid rgb(32, 32, 32);
  background-color: #242830;
}

#E-Panel-Login-Left>img {
  margin-bottom: 5%;
}

#E-Panel-Login-Right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 50vw;
  max-width: 50vw;
  height: 100%;

  margin-top: -7.5%;
  margin-bottom: -2.5%;
}

#E-Panel-Login-Right>h1 {
  margin: 5% 0;
  padding-top: 5%;
}

#E-Panel-Login-Right>div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  margin-top: 5%;
}

#E-Panel-Login-Right>div>button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  min-width: 148px;
  padding: 16px;

  color: rgb(168, 114, 64);
  border-radius: 6px;
  border: 2px solid rgb(168, 114, 64);
  background-color: #f0f0f0;
}

#E-Panel-Login-Right>div>button:hover {
  color: #f0f0f0;
  background-color: rgb(168, 114, 64);
}

#E-Panel-Login-Right>input {
  z-index: 1;

  width: 50%;
  max-height: 42px;
  min-height: 42px;
  margin: 0 auto 16px auto;
  padding: 16px 16px 0 16px;
  border: 0 solid #fff;
  inset: none;

  border-radius: 6px;
  -webkit-appearance: none;
  -webkit-border-radius: 6px;
}

#E-Panel-Login-Right>p {
  z-index: 2;
  user-select: none;
  pointer-events: none;

  width: 50%;
  height: 32px;
  margin: 16px 0 -36px 0;
  padding: 0 8px 0 8px;

  color:rgb(85, 85, 85);
  border-radius: 6px;
}

#E-Panel-Login-Right>p>img {
  user-select: none;
  pointer-events: none;
  margin: 0 12px -2px 0;
}

#E-Panel-Login-Right>a {
  margin: 5% 0 -10% 0;
  padding-bottom: 10%;
}

.login-error {
  text-align: center;
  padding: 8px 0 0 0 !important;
  margin: 0 auto 2% auto !important;
  color: white !important;
  background-color: red;
}

@media screen and (max-width: 1250px) {
  #E-Panel-Login { flex-direction: column !important; }
  #E-Panel-Login-Left { display: none !important; }
  #E-Panel-Login-Right { min-width: 100vw !important; margin-top: -15vh; }
  #Panel-Content-Container { flex-direction: column; }
  #Panel-Content-Right, #Panel-Content-Left { margin: 32px 0; width: 100%; }
}

@media screen and (max-width: 620px) {
  #E-Panel-Login-Right>h1 { padding-top: unset; }
  #E-Panel-Login-Right>div { flex-direction: column; }
  #E-Panel-Login-Right>div>button { margin-bottom: 32px; }
}


/* PANEL SELECT VIEW */

.ep-panel-btn {
  width: 100%;
  max-width: 128px;
  height: 32px;
  margin: 64px 0 0 0;
  padding: 0 16px;
  font-size: large;
}

.ep-bottom-btn {
  color: #282c34;
  cursor: pointer;
  margin: 0 auto 0 auto;
  max-width: fit-content;
  cursor: pointer;
  border-bottom: 2px solid transparent;
}

.ep-bottom-btn:hover {
  border-bottom: 2px solid #242830;
}

.ep-login-panel-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start !important;

  min-height: 25vh;
  max-height: 50vh;
  overflow-y: auto;

  border-radius: 5px;
  border: 2px solid rgb(160, 160, 160);
}

.ep-login-panel-btn {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  width: 85%;
  background-color: #282c34;
  border-radius: 3px;
  padding: 16px;
  margin: 16px 0;
}

.ep-login-panel-btn:hover {
  background-color: #363b46;
}

.ep-login-panel-btn>img {
  user-select: none;
  min-width: 48px;
  min-height: 48px;
  max-width: 48px;
  max-height: 48px;
  margin: auto 0 auto 16px;
}

.ep-login-panel-btn>p {
  user-select: none;
  margin: auto 0 auto 32px;
}

.full-modal-options>button {
  color: #363b46;
  width: 198px;
  height: 36px;
  font-size: 18px;
  border-radius: 3px;
  background-color: #ececec;
}


.tttt {
  color: rgb(99, 33, 33);
}
