
img {
  user-select: none;
  pointer-events: none;
}

hr {
  min-width: 90%;
  margin: 0 0 0 2.5% 0;
}

p {
  color: whitesmoke;
  margin: 0 5% 2.5% 5%;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
}

.link {
  color: #61dafb;
}

.logo {
  height: 40vmin;
  pointer-events: none;
}

.display-box-row {
  margin:  0 2%;
  border-top: 2px solid rgb(126, 126, 126);
  border-bottom: 2px solid rgb(126, 126, 126);
}

.home-section {
  display: flex;
  flex-direction: row;

  padding: 0 0 0 0;

  background-size: 100%;
  background-repeat: no-repeat;
  border-bottom: 2px solid rgb(126, 126, 126);
}

.home-section-left {
  display: flex;
  flex-direction: column;

  text-align: left;
  margin: 0 auto 0 5%;
  min-width: 40%;
  width: 40%;
}

.home-section-text {
  min-height: 100%;
  background-color: rgba(24, 24, 24, 0.75);
}

.home-section-right {
  display: flex;
  flex-direction: column;

  text-align: left;
  margin: auto 5% auto auto;
  min-width: 40%;
  max-width: 40%;
}

.home-section-image {
  margin: auto auto;
  filter: blur(5px);
}

#social-footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 86px;
  margin-top: 5%;
}

#social-footer>a>img {
  width: 15%;
}
